var _              = require('underscore');
var React          = require('react');
var ReactBootstrap = require('react-bootstrap');
var Col            = ReactBootstrap.Col;
var Row            = ReactBootstrap.Row;
var Button         = ReactBootstrap.Button;

var DartBoardActions = require('../actions/DartBoardActions');
var PlayerInput      = require('./PlayerInput.react');

function computeCols(s){
    return _.map(s.props.zones, function(zone,key) {
      var classSup    = '';
      var zoneContent = zone;
      if(zone == 'bulle') {
        classSup    ='BulleHeader';
        zoneContent = React.createElement("i", {className: "glyphicon glyphicon-record"});
      }
      return React.createElement(Col, {md: 1, xs: 1, key: zone, className:  classSup + ' h2 text-center'}, zoneContent )
    }, s);
}

var Header = React.createClass({displayName: "Header",

  render: function() {
    var cols = computeCols(this);

    return (
      React.createElement(Row, {id: "header", componentClass: "header"}, 
        React.createElement(Col, {md: 3, xs: 3 }, 
          React.createElement(PlayerInput, {id: "new-player", placeholder: "Nom du joueur", playernum:  this.props.playernum, onSave:  this._onSave})
        ), 
        cols, 
        React.createElement(Col, {md: 2, xs: 2, className: "h2 text-center"}, 
          React.createElement(Button, {bsStyle: 'default', onClick:  this._cleanBoard}, 
            React.createElement("span", {className: "glyphicon glyphicon-refresh"})
          )
        )
      )
    );
  },

  _onSave: function(name) {
    DartBoardActions.createPlayer(name);
  },

  _cleanBoard: function(){
    this.props.onCleanBoard();
  }

});

module.exports = Header;
