var _                       = require('underscore');
var React                   = require('react/addons');
var ReactBootstrap          = require('react-bootstrap');
var ReactCSSTransitionGroup = React.addons.CSSTransitionGroup;
var Col                     = ReactBootstrap.Col;
var Button                  = ReactBootstrap.Button;

var DartBoardActions = require('../actions/DartBoardActions');

var Header = React.createClass({displayName: "Header",
  render: function() {
    return (
      React.createElement("footer", {id: "footer", className: "row"}, 
        React.createElement(Col, {md: 7, xs: 7, mdOffset: 3, xsOffset: 3 }, 
            React.createElement(Button, {ref: "NextPlayerBtn", bsStyle: 'success', onClick:  this._onClick, className: "btn-lg btn-block animated flipInX "}, 
              React.createElement("span", {className: "glyphicon glyphicon-chevron-down"}), 
              "Joueur suivant"
            )
        )
      )
    );
  },

  /**
   * @param {string} name
   */
  _onClick: function() {
    DartBoardActions.selectNextPlayer();
  }

});

module.exports = Header;
